@import url('https://fonts.googleapis.com/css2?family=Source+Code+Pro:wght@400;700&display=swap');

/* === En-tête fixe === */
.header {
  width: 100%;
  top: 0;
  z-index: 1000;
  transition: background-color 0.3s ease, box-shadow 0.3s ease;
  background-color: transparent;
}

/* === Changement de couleur au scroll === */
.navbarScroll.navbarDark {
  background-color: #171010;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.3);
}

/* === Barre de navigation principale === */
.navbar {
  font-family: 'Source Code Pro', monospace;
  padding: 1rem 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

/* === Conteneur du logo + menu === */
.container {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}

/* === Logo === */
.logo img {
  width: 250px;
  height: 100px;
  transition: transform 0.2s ease;
}

.logo img:hover {
  transform: scale(1.05);
}

/* === Menu horizontal === */
.navbar ul {
  list-style: none;
  display: flex;
  gap: 2rem;
  margin: 0;
  padding: 0;
  align-items: center;
}

.navbar ul li {
  display: flex;
}

/* === Liens === */
.navbar ul li a {
  font-size: 1rem;
  color: #000;
  text-decoration: none;
  display: inline-flex;
  align-items: center;
  padding: 0.5rem 0;
  position: relative;
  transition: color 0.2s ease;
}

.navbar ul li a:hover {
  color: #001822;
}

.navbar ul li a.active {
  color: #09232e;
}

.navbar ul li a::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: -2px;
  height: 2px;
  width: 0%;
  background-color: #000000;
  transition: width 0.3s ease;
}

.navbar ul li a:hover::after,
.navbar ul li a.active::after {
  width: 100%;
}

/* === Responsive horizontal (pas de colonne) === */
@media screen and (max-width: 800px) {
  .navbar ul {
    gap: 1.2rem; /* Réduction de l’espacement */
  }

  .navbar ul li a {
    font-size: 14px;
    padding: 0.3rem 0;
  }

  .logo img {
    width: 250px;
    height: 100px;
  }
}

@media screen and (max-width: 400px) {
  .navbar ul {
    gap: 0.8rem;
  }

  .navbar ul li a {
    font-size: 12px;
    padding-left: 0;
  }
}

