
.banner {
    position: relative;
    width: 100%;
    max-width: 100%;
    margin: 0 auto;
    border-radius: 25px;
  }
  
  .banner-image {
    width: 100%;
    max-width: 100%;
    object-fit: cover;
    min-height: 111px;
    mask-image: linear-gradient(to right, 
    transparent 0%, 
    black 15%, 
    black 85%, 
    transparent 100%);
  -webkit-mask-image: linear-gradient(to right, 
    transparent 0%, 
    black 15%, 
    black 85%, 
    transparent 100%);
  }
  
  @media screen and (max-width: 960px) {
    .banner-text {
      font-size: 24px;
      line-height: 24px;
      letter-spacing: 0em;
      text-align: left;
    }
  
    .banner {
      margin-top: 19px;
    }
  
    .text-mobile-split {
      display: block;
    }
  }
  
  @media screen and (max-width: 320px) {
    .banner-text {
      font-size: 20px;
      line-height: 20px;
    }
  }