@import url('https://fonts.googleapis.com/css2?family=JetBrains+Mono:wght@400;700&display=swap');

.factory-pipeline {
  position: relative;
  background-color: white;
  padding: 2rem;
  border-radius: 12px;
  color: white;
  overflow: hidden;
  font-family: 'JetBrains Mono', 'Courier New', monospace;
}

.machine-section {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 2rem;
  margin-bottom: 1rem;
}

.machine {
  color: #101011;
  padding: 0.8rem 1.5rem;
  font-weight: bold;
}

.gear {
  font-size: 2rem;
}

.rotating {
  animation: spin 4s linear infinite;
}

.reverse {
  animation-direction: reverse;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.conveyor {
  position: relative;
  height: 80px;
  background: #101011;
  border-radius: 10px;
  overflow: hidden;
  mask-image: linear-gradient(to right, 
    transparent 0%, 
    black 15%, 
    black 85%, 
    transparent 100%);
  -webkit-mask-image: linear-gradient(to right, 
    transparent 0%, 
    black 15%, 
    black 85%, 
    transparent 100%);
}

.item {
  position: absolute;
  top: 20px;
  left: -150px;
  animation: move 8s linear forwards;
  display: flex;
  gap: 1rem;
  padding: 0.6rem 1rem;
  border-radius: 8px;
  font-weight: bold;
  align-items: center;
}

.item .feature {
  font-size: 1rem;
}

.item .status {
  font-size: 1.3rem;
}

.item.passed {
  background-color: #15803d;
  color: #d1fae5;
}

.item.failed {
  background-color: #991b1b;
  color: #fee2e2;
}

@keyframes move {
  0% { left: -150px; opacity: 0; }
  10% { opacity: 1; }
  90% { left: 80%; opacity: 1; }
  100% { left: 100%; opacity: 0; }
}

  
  